import { Link } from 'react-router-dom';
import { HashLink } from 'react-router-hash-link';
import {useEffect} from 'react';
import { LoginTitle } from '../loginTitle/LoginTitle';
import { Navigation } from 'swiper';

import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/navigation';

import './Login.scss';
import instagram from '../../assets/img/instagram.svg'
import telegram from '../../assets/img/telegram.svg'
import aboutTitle from '../../assets/img/about.png';
import aboutGirl from '../../assets/img/about_girl.jpg';
import rateTitle from '../../assets/img/rate.png';
import reviewTitle from '../../assets/img/review.png';

import reviewImg1 from '../../assets/img/review1.jpg' 
import reviewImg2 from '../../assets/img/review2.jpg' 
import reviewImg3 from '../../assets/img/review3.jpg' 

import logo from '../../assets/img/logo.png';

const Login = (props) => {

    useEffect(() => {
        console.log(props.users);
    }, [props.users])
    
    return (
        <>
        <section className="login">
            <div className="container">
                <div className="login__container">
                    <nav className="login__nav">
                        <Link to="/#about" className="login__anchor"> </Link>
                        <a href="#rate" className="login__anchor"></a>
                        <a href="#review" className="login__anchor"></a>
                    </nav>
                    <Link to={localStorage.getItem('You.Course__login') ? '/links' : "/login"} className="login__login"></Link>
                </div> 
                <div className="login__links-container">
                    <a href='#' className="login__link">
                        <img src={instagram} alt="Instagram" className="login__link-img" />
                    </a>
                    <a href='#' className="login__link">
                        <img src={telegram} alt="Telegram" className="login__link-img" />
                    </a>
                </div>   
            </div>
        </section>
        <section className="about" id="about">
            <img className='about__title' src={aboutTitle} alt=" " />
            <div className="container">
                <div className="about__flex">
                    <div className="about__item">
                        <div className="about__name">!   <span> </span></div>
                        <div className="about__descr">     .      ,    </div>
                        <div className="about__works">
                            <div className="about__work-title">   :</div>
                            <div className="about__work-container">
                                <div className="about__work-item">     </div>
                                <div className="about__work-item">      </div>
                                <div className="about__work-item">   </div>
                            </div>
                        </div>
                    </div>
                    <div className="about__item">
                        <img src={aboutGirl} alt="" className="about__master" />
                    </div>
                </div>
            </div>
        </section>
        <section className="rate">
            <div className="container">
                {LoginTitle(rateTitle, '')}
                <div className="rate__container">
                    <div className="rate__item">
                        <div className="rate__data-container">
                            <div className="rate__name"></div>
                            <div className="rate__descr">  21 </div>
                            <ul className="rate__functions">
                                <li className="rate__function">    </li>
                                <li className="rate__function"> </li>
                                <li className="rate__function">  </li>
                                <li className="rate__function">      </li>
                                <li className="rate__function">   </li>
                                <li className="rate__function">      </li>
                                <li className="rate__function">    </li>
                                <li className="rate__function"></li>
                            </ul>
                        </div>
                        <div className="rate__price-container">
                            <div className="rate__price">3000</div>
                            <div className="rate__subprice"> 2800</div>
                        </div>
                    </div>
                    <div className="rate__item">
                        <div className="rate__data-container">
                            <div className="rate__name"> </div>
                            <div className="rate__descr">  21 </div>
                            <ul className="rate__functions">
                                <li className="rate__function">  , ,   </li>
                                <li className="rate__function"> </li>
                            </ul>
                        </div>
                        <div className="rate__price-container">
                            <div className="rate__price">3000</div>
                            <div className="rate__subprice"> 2800</div>
                        </div>
                    </div>
                    <div className="rate__item">
                        <div className="rate__data-container">
                            <div className="rate__name"></div>
                            <div className="rate__descr"></div>
                            <ul className="rate__functions">
                                <li className="rate__function">     : , , </li>
                                <li className="rate__function">   ,     ?,    ?</li>
                                <li className="rate__function">                </li>
                            </ul>
                        </div>
                        <div className="rate__price-container">
                            <div className="rate__price">1000</div>
                            <div className="rate__subprice"> 800</div>
                        </div>
                    </div>
                    <div className="rate__item">
                        <div className="rate__data-container">
                            <div className="rate__name"> </div>
                            <div className="rate__descr"></div>
                            <ul className="rate__functions">
                                <li className="rate__function">  ,   (  )</li>
                                <li className="rate__function">,   (, ,   . )</li>
                                <li className="rate__function">     (  ,   3     30 )</li>
                                <li className="rate__function">  1-2           ,       </li>
                            </ul>
                        </div>
                        <div className="rate__price-container">
                            <div className="rate__price">1000</div>
                            <div className="rate__subprice"> 800</div>
                        </div>
                    </div>
                </div>
                <div className="rate__links">
                    <div className="rate__link-item">  </div>
                    <div className="rate__link-item">
                        <a href='#' className="rate__link">
                            <img src={telegram} alt="Telegram" className="rate__link-img" />
                        </a>
                    </div>
                    <div className="rate__link-item">
                        <a href='#' className="rate__link">
                            <img src={instagram} alt="Instagram" className="rate__link-img" />
                        </a>
                    </div>
                </div>
            </div>
        </section>
        <section className="review">
            <div className="container">
                {LoginTitle(reviewTitle, '')}
                <Swiper
                modules={[Navigation]}
                spaceBetween={0}
                slidesPerView={3}
                /* width={'80%'} */
                onSlideChange={() => console.log('slide change')}
                onSwiper={(swiper) => console.log(swiper)}
                navigation
                centeredSlides={true}
                loop={true}
                allowTouchMove={false}
                className='review__slider'
                >
                    <SwiperSlide><img src={reviewImg1} alt="" className="review__img" /></SwiperSlide>
                    <SwiperSlide><img src={reviewImg2} alt="" className="review__img" /></SwiperSlide>
                    <SwiperSlide><img src={reviewImg3} alt="" className="review__img" /></SwiperSlide>
                    
                </Swiper>
            </div>
        </section>
        <footer className='footer'>
            <div className="container">
                <div className="footer__inner">
                    <div className="footer__links">
                        <div className="footer__link">
                            <img src={telegram} alt="Telegram" className="footer__link-img" />
                        </div>
                        <div className="footer__link">
                            <img src={instagram} alt="Instagram" className="footer__link-img" />
                        </div>
                    </div>
                    <img src={logo} alt="Logo" className="footer__logo" />
                    <div className="footer__credentials">&copy; {new Date().getFullYear()} You.Course</div>
                </div>
            </div>
        </footer>
        </>
    )
}

export default Login;